"use client";

import { useState } from "react";
import { motion } from "framer-motion";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Card, CardContent } from "@/components/ui/card";
import { format } from "date-fns";
import { Plus, Trash2} from "lucide-react";
import { useContract } from "@/context/contract-context";
import { toast } from "sonner";

export default function CreateVoteForm() {

  const {contract} = useContract();
  const [formData, setFormData] = useState({
    description: "",
    restriction: "0", // OpenToAll (default)
    resultType: "0", // RankBased (default)
    startTime: "",
    endTime: "",
    maxVoters: "0",
    proposals: ["", ""] // minimum 2 proposals required
  });

  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSelectChange = (name, value) => {
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleProposalChange = (index, value) => {
    const updatedProposals = [...formData.proposals];
    updatedProposals[index] = value;
    setFormData((prev) => ({ ...prev, proposals: updatedProposals }));
  };

  const addProposal = () => {
    setFormData((prev) => ({
      ...prev,
      proposals: [...prev.proposals, ""]
    }));
  };

  const removeProposal = (index) => {
    if (formData.proposals.length <= 2) return; // Minimum 2 proposals required
    const updatedProposals = [...formData.proposals];
    updatedProposals.splice(index, 1);
    setFormData((prev) => ({ ...prev, proposals: updatedProposals }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    try {
      // Validate required fields
      const validProposals = formData.proposals.filter(p => p.trim());
      if (validProposals.length < 2) {
        toast.error("At least 2 valid proposals are required");
        return;
      }
      
      // Convert times to Unix timestamps
      const startTimestamp = Math.floor(new Date(formData.startTime).getTime() / 1000);
      const endTimestamp = Math.floor(new Date(formData.endTime).getTime() / 1000);
      
      // Convert numeric string values to actual numbers
      const restrictionValue = parseInt(formData.restriction);
      const resultTypeValue = parseInt(formData.resultType);
      const maxVotersValue = parseInt(formData.maxVoters) || 0;
      
      // Filter out empty proposals
      const filteredProposals = formData.proposals.filter(p => p.trim());
      
      console.log("Sending to contract:", {
        description: formData.description,
        restriction: restrictionValue,
        resultType: resultTypeValue,
        startTime: startTimestamp,
        endTime: endTimestamp,
        maxVoters: maxVotersValue,
        proposals: filteredProposals
      });
      
      // Call the contract method with properly formatted data
      const tx = await contract.createProposalBasedCampaign(
        formData.description,
        restrictionValue,
        resultTypeValue,
        startTimestamp,
        endTimestamp,
        maxVotersValue,
        filteredProposals
      );
      
      // Wait for transaction to be mined
      await tx.wait();
      
      toast.success("Campaign created successfully!");
      
      // Reset form
      setFormData({
        description: "",
        restriction: "0",
        resultType: "0",
        startTime: "",
        endTime: "",
        maxVoters: "0",
        proposals: ["", ""]
      });
      
    } catch (error) {
      console.error("Error creating campaign:", error);
      toast.error("Error creating campaign. Please try again.");
    } finally {
      setIsSubmitting(false);
    }
  };
  // Calculate minimum start time (current time + 3 minutes as per contract)
  const minStartTime = new Date();
  minStartTime.setMinutes(minStartTime.getMinutes() + 3);
  const minStartTimeStr = format(minStartTime, "yyyy-MM-dd'T'HH:mm");

  // If start time is set, end time must be after it
  const minEndTimeStr = formData.startTime 
    ? format(new Date(new Date(formData.startTime).getTime() + 60000), "yyyy-MM-dd'T'HH:mm") 
    : minStartTimeStr;

  return (
    <motion.form 
      initial={{ opacity: 0 }} 
      animate={{ opacity: 1 }}
      onSubmit={handleSubmit}
      className="space-y-8 max-w-3xl mx-auto"
    >
      {/* Campaign Details */}
      <div className="space-y-4">
        <h2 className="text-2xl font-semibold">Campaign Details</h2>
        
        <div className="space-y-2">
          <Label htmlFor="description">Campaign Description</Label>
          <Textarea 
            id="description" 
            name="description" 
            placeholder="Describe what this campaign is about..." 
            value={formData.description}
            onChange={handleChange}
            required
            className="min-h-32"
          />
        </div>
      </div>

      {/* Voting Configuration */}
      <div className="space-y-4">
        <h2 className="text-2xl font-semibold">Voting Configuration</h2>
        
        <div className="space-y-2">
          <Label>Voting Restriction</Label>
          <Select 
            value={formData.restriction} 
            onValueChange={(value) => handleSelectChange("restriction", value)}
          >
            <SelectTrigger>
              <SelectValue placeholder="Select voting restriction" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="0">Open to All</SelectItem>
              <SelectItem value="1">Limited Number of Voters</SelectItem>
              <SelectItem value="2">Registration Required</SelectItem>
            </SelectContent>
          </Select>
        </div>

        {(formData.restriction === "1" || formData.restriction === "2") && (
          <div className="space-y-2">
            <Label htmlFor="maxVoters">Maximum Number of Voters</Label>
            <Input 
              id="maxVoters" 
              name="maxVoters" 
              type="number" 
              min="1"
              value={formData.maxVoters}
              onChange={handleChange}
              required={formData.restriction === "1"}
            />
          </div>
        )}
        
        <div className="space-y-2">
          <Label>Result Type</Label>
          <RadioGroup 
            value={formData.resultType}
            onValueChange={(value) => handleSelectChange("resultType", value)}
            className="flex space-x-4"
          >
            <div className="flex items-center space-x-2">
              <RadioGroupItem value="0" id="rank-based" />
              <Label htmlFor="rank-based">Rank Based</Label>
            </div>
            <div className="flex items-center space-x-2">
              <RadioGroupItem value="1" id="one-winner" />
              <Label htmlFor="one-winner">One Winner</Label>
            </div>
          </RadioGroup>
        </div>
      </div>

      {/* Time Frame */}
      <div className="space-y-4">
        <h2 className="text-2xl font-semibold">Time Frame</h2>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="space-y-2">
            <Label htmlFor="startTime">Start Time</Label>
            <Input 
              id="startTime" 
              name="startTime" 
              type="datetime-local" 
              min={minStartTimeStr}
              value={formData.startTime}
              onChange={handleChange}
              required
            />
            <p className="text-xs text-muted-foreground">Must be at least 3 minutes from now</p>
          </div>
          
          <div className="space-y-2">
            <Label htmlFor="endTime">End Time</Label>
            <Input 
              id="endTime" 
              name="endTime" 
              type="datetime-local" 
              min={minEndTimeStr}
              value={formData.endTime}
              onChange={handleChange}
              required
            />
          </div>
        </div>
      </div>

      {/* Proposals */}
      <div className="space-y-4">
        <div className="flex justify-between items-center">
          <h2 className="text-2xl font-semibold">Proposals</h2>
          <Button 
            type="button" 
            variant="outline" 
            size="sm" 
            onClick={addProposal}
          >
            <Plus className="h-4 w-4 mr-2" /> Add Proposal
          </Button>
        </div>
        
        <div className="space-y-3">
          {formData.proposals.map((proposal, index) => (
            <div key={index} className="flex gap-2">
              <Input 
                placeholder={`Proposal ${index + 1}`}
                value={proposal}
                onChange={(e) => handleProposalChange(index, e.target.value)}
                required
                className="flex-1"
              />
              <Button 
                type="button" 
                variant="ghost" 
                size="icon"
                onClick={() => removeProposal(index)}
                disabled={formData.proposals.length <= 2}
              >
                <Trash2 className="h-4 w-4" />
              </Button>
            </div>
          ))}
        </div>
        <p className="text-sm text-muted-foreground">Minimum 2 proposals required</p>
      </div>

      {/* Preview */}
      <Card>
        <CardContent className="pt-6">
          <h3 className="font-medium mb-2">Campaign Preview</h3>
          <div className="text-sm space-y-1 text-muted-foreground">
            <p><strong>Description:</strong> {formData.description || "No description provided"}</p>
            <p><strong>Voting:</strong> {
              formData.restriction === "0" ? "Open to All" : 
              formData.restriction === "1" ? `Limited to ${formData.maxVoters} voters` : 
              "Registration Required"
            }</p>
            <p><strong>Result Type:</strong> {formData.resultType === "0" ? "Rank Based" : "One Winner"}</p>
            <p><strong>Duration:</strong> {
              formData.startTime && formData.endTime ? 
              `${new Date(formData.startTime).toLocaleString()} to ${new Date(formData.endTime).toLocaleString()}` : 
              "Time frame not set"
            }</p>
            <p><strong>Proposals:</strong> {formData.proposals.filter(p => p.trim()).length} valid proposals</p>
          </div>
        </CardContent>
      </Card>

      {/* Submit Button */}
      <div className="pt-4">
        <Button 
          type="submit" 
          className="w-full" 
          disabled={isSubmitting}
        >
          {isSubmitting ? 
            "Creating Campaign..." : 
            "Create Campaign"
          }
        </Button>
      </div>
    </motion.form>
  );
}